import { login } from '@/utils/utils.js';
import uniFab from '@/components/uni-fab/uni-fab.vue';
import uniNavBar from "@/components/uni-nav-bar/uni-nav-bar.vue"
export default {
	components: {
		uniFab, uniNavBar
	},
	data() {
		return {
			userInfo:{
				name:"",
				avatarUrl:"",
			},
			buttonArray: [{
						text: '制作',
						active: false
					},
					{
						text: '客服',
						active: true
					}
			],
			background: ['color1', 'color2', 'color3'],
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500,
			buttonPattern:{
				backgroundColor: "#ffffff",
				buttonColor: "#4169E1",
				color: "#696969",
				selectedColor: "#007AFF"
			}
		};
	},
	onLoad() {
		// this.getUserInfo();
		this.gotologin();
	},
	methods: {
		enterCenter(){
			console.log("进入个人中心页面");
			uni.navigateTo({
				url:"../../center/center"
			})
		},
		clickButton(){
			console.log("展开菜单");
		},
		clickText(e){
			console.log("选择制作方式");
			console.log(e);
			if(e.index==0){
				uni.navigateTo({
					url: "/pages/tabbar/tabbar-2/tabbar-2"
				})
			}
		},
		getUserInfo(){
			var self = this;
			wx.getUserInfo({
					success(res) {
						console.log("获取用户信息！！");
						console.log(res);
						self.userInfo.name = res.userInfo.nickName;
						self.userInfo.avatarUrl = res.userInfo.avatarUrl
					}
			});
		},
		gotologin(){
			var self = this;
			
			//调用wx.login获取code
			wx.login({
			  // callback
			  success (res) {
				console.log("微信开放接口api")
				console.log(res);
			    if (res.code) {
					console.log("开始在用户服务器上登录")
					self.$store.state.userInfo.code = res.code;
					console.log(res)
			        //用获取的临时登录凭证code在业务服务器上换取openID
					// self.$http.post(self.$store.state.url.LOGIN,{code:res.code})
					login({code:res.code}).then(res=>{
						console.log("登录成功");
						console.log(res);
						// self.$store.state.userInfo.token = res.data.token;
						self.$store.commit('changeItem',{
							page:'userInfo',
							key:'userId',
							value:res.data.user.userId
						})
						// self.$store.state.userInfo.userId = res.data.user.userId;
						// self.$store.state.userInfo.openid = res.data.user.openid;
						// console.log(self.$store.state.userInfo);
				    })
				    .catch(err=>{
					    console.log("登录失败");
					    console.log(err);
				    })
			    } else {
			      console.log('微信开放接口失败' + res.errMsg)
			    }
			  }
			})
		}
	}
};
